import { profile } from '@/shared/api';
import { $$alerts, $$contacts } from '@/shared/effector';
import { attach, createEvent, createStore, sample } from 'effector';
import { reset } from 'patronum';
var updateFx = attach({ effect: profile.updateAvatar });
var $avatar = createStore(null);
var avatarChanged = createEvent();
$avatar.on(avatarChanged, function (_, avatar) { return avatar; });
var $pending = updateFx.pending;
var $error = createStore(null);
var submited = createEvent();
var reseted = createEvent();
// Submited
sample({
    clock: submited,
    source: {
        avatar: $avatar,
    },
    filter: function (_a) {
        var avatar = _a.avatar;
        return !!avatar;
    },
    fn: function (contact) { return ({ avatar: contact.avatar }); },
    target: updateFx,
});
// Done
sample({
    clock: updateFx.done,
    fn: function () { return 'Аватар изменен'; },
    target: $$alerts.showSuccess,
});
sample({
    clock: updateFx.done,
    target: $$contacts.reload,
});
// Failed
sample({
    clock: updateFx.fail,
    fn: function () { return 'Аватар не изменен'; },
    target: $$alerts.showDanger,
});
// Reseted
reset({
    clock: reseted,
    target: [$avatar],
});
reset({
    clock: [],
    target: $error,
});
export var $$profileAvatar = {
    $avatar: $avatar,
    avatarChanged: avatarChanged,
    $error: $error,
    $pending: $pending,
    submited: submited,
    reseted: reseted,
};
